@use './config' as g;
.sec-join {
	position: relative;
	overflow: hidden;
  .m-sec__inner {
		padding-bottom: 48px;
	}
}
.o-join__text {
	color: var(--lp-style-key-black, #111);
	text-align: center;
	font-size: 16px;
	font-style: normal;
	font-weight: 600;
	line-height: 1.8; /* 28.8px */
	margin-top: 32px;
}

.o-join__content {
	margin-top: 32px;
	display: flex;
	flex-wrap: wrap;
	gap: 32px;
}
.m-join__contents {
	flex: auto;
	padding: 0 0 36px;
	align-self: stretch;
	position: relative;
	border-radius: 18px;
	border: 3px solid var(--lp-style-key-yellow, #f2ff59);
	background: var(--lp-style-key-aque, #93e5e5);
	// overflow: hidden;
	& > * {
		position: relative;
	}
  .--bgwrap{
    padding: 28px 22px 32px;
    width: 100%;
    overflow: hidden;
    position: relative;
    z-index: 1;
    border-radius: 18px 18px 0 0;
    &::before {
      content: '';
      position: absolute;
      left: -60px;
      top: -30px;
      display: block;
      background-image: url(../img/bg_box_join.png);
      background-repeat: no-repeat;
      background-size: contain;
      width: 560px;
      height: 421px;
      // transform: rotate(10deg);
      opacity: 0.7;
      z-index: -1;
    }
  }
	h3 {
    max-width: 230px;
    margin: auto;
		// color: var(--lp-style-key-black, #111);
		// font-family: g.$fontFamily__Heading;
		// font-size: 22px;
		// font-style: normal;
		// font-weight: 900;
		// line-height: 1.4;
		// letter-spacing: -0.44px;
		// .--outline {
		// 	color: var(--lp-style-key-yellow, #f2ff59);
		// 	font-size: 30px;
		// 	font-weight: 900;
		// 	line-height: 1.4; /* 42px */
		// 	letter-spacing: -0.6px;
		// 	text-shadow: 2px 2px 0px #111, -2px 2px 0px #111, 2px -2px 0px #111,
		// 		-2px -2px 0px #111, 2px 0px 0px #111, 0px 2px 0px #111,
		// 		-2px 0px 0px #111, 0px -2px 0px #111;
		// }
	}
  &:nth-child(1) h3{
    max-width: 270px;
  }
	p {
		color: var(--lp-style-key-black, #111);
		text-align: center;
		font-size: 14px;
		font-style: normal;
		font-weight: 600;
		line-height: 1; /* 14px */
		letter-spacing: -0.14px;
		display: flex;
		height: 24px;
		padding: 4px 6px 3px 6px;
		justify-content: center;
		align-items: center;
		align-self: stretch;
		background: var(--lp-style-key-white, #fff);
		margin-top: 18px;
	}
}
.m-join__contents__img {
	width: 100%;
	max-width: 244px;
	margin: auto;
	margin-top: 10px;
	filter: drop-shadow(0px 4px 2px rgba(0, 0, 0, 0.25));
}
.o-join__linkwrap {
	width: 308px;
	display: flex;
	justify-content: center;
	margin: auto;
	margin: 0 calc(50% - 154px);
	// margin-top: 32px;
	gap: 32px;
}
.m-join__link {
	width: 138px;
	height: 140px;
	border-radius: 50%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	gap: 5px;
	position: relative;
	z-index: 1;
	&::before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		margin: auto;
    margin: calc(50% - 80px) calc(50% - 82px);
		width: 164px;
		height: 166px;
		// transform: rotate(20deg);
		background-size: contain;
		background-repeat: no-repeat;
    z-index: -1;
	}
	.--label {
		color: var(--lp-style-key-white, #fff);
		font-family: g.$fontFamily__Heading;
		font-size: 16px;
		font-style: normal;
		font-weight: 700;
		line-height: 1.3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 85px;
	}
	.--em {
		font-size: 24px;
	}
	i {
		display: block;
		width: 20px;
		height: 20px;
		background-image: url(../img/icon_arrow_white.svg);
		background-repeat: no-repeat;
		background-size: contain;
	}
	&.--senmon {
		background: #e065be;
    &::before {
      background-image: url(../img/box_join_btn_pink.svg);
    }
	}
	&.--hs {
    background: #7e65df;
    &::before {
      background-image: url(../img/box_join_btn_mauve.svg);
    }
	}
}
