//===Color-Picker-LIST===//

$baseColor__black: #111;




$baseColor__gray: rgba(0, 0, 0, 0.06);
$baseColor__gray--2: #F8F8F8;

$baseColor__black--100: #1f1f1f;
$baseColor__white: #fff;
$baseColor__gray: #f6f6f6;
$baseColor__gray--300: #aaaaaa;
$baseColor__gray--400: #5a5a5a;

$keyColor: #E44E2E;

$keyColor__middle: #bae3dd;
$keyColor__light: #e7f4f2;
$keyColor__highlight: #fff500;

$keyColor__gradation: linear-gradient(0deg, $keyColor -26.04%, #30a9c6 59.14%);
$keyColor__gradation--middle: linear-gradient(
  326.5deg,
  #00b097 5.86%,
  #30a9c6 92.39%
);

//===@media===//
$maxW__pc:  500px;
$minW__pc:  501px;

//===Font-Family===//
$fontFamily__base: "Hiragino sans", "ヒラギノ角ゴシック", 游ゴシック, "Yu Gothic", yugothic, sans-serif;
$fontFamily__Heading: "Noto Sans JP", sans-serif;
$fontFamily__Alphabet: "Barlow Condensed", sans-serif;
$fontFamily__Arial: "Arial", sans-serif;
$fontFamily__Outfit:  'Outfit', sans-serif;

//===heading-decoration===//
$heading-deco_height: 5px;


