@use './config' as g;
.sec-event {
	position: relative;
	overflow: hidden;
	.m-sec__inner {
		padding-bottom: 48px;
	}
}
.o-event__text {
	color: var(--lp-style-key-black, #111);
	text-align: center;
	font-size: 16px;
	font-style: normal;
	font-weight: 600;
	line-height: 1.8; /* 28.8px */
	margin-top: 32px;
}

.o-event__content {
	margin-top: 32px;
	display: flex;
	flex-wrap: wrap;
	gap: 32px;
}
.m-event__contents {
	padding: 28px 22px;
	align-self: stretch;
	position: relative;
	border-radius: 18px;
	border: 3px solid var(--lp-style-key-yellow, #f2ff59);
	background: var(--lp-style-key-aque, #93e5e5);
	overflow: hidden;
	& > * {
		position: relative;
	}
	&::before {
		content: '';
		position: absolute;
		left: -91px;
		top: -153px;
		display: block;
		background-image: url(../img/bg_box_event.png);
		background-repeat: no-repeat;
		background-size: contain;
		width: 560px;
		height: 421px;
		transform: rotate(10deg);
		opacity: 0.7;
	}
	h3 {
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 42px;
		border: 2px solid var(--lp-style-key-white, #fff);
		background: var(--lp-style-key-white, #fff);
		color: var(--lp-style-key-magenta, #e065be);
		text-align: center;
		font-family: g.$fontFamily__Heading;
		font-size: 22px;
		font-style: normal;
		font-weight: 900;
		line-height: 1.4; /* 30.8px */
	}
	p {
		text-align: left;
		color: var(--lp-style-key-black, #111);
		font-size: 15px;
		font-style: normal;
		line-height: 1.8; /* 27px */
		margin-top: 18px;
	}
}
.m-event__contents__img {
	overflow: hidden;
	border-radius: 50%;
	border: solid 8px #fff;
	width: 100%;
	max-width: 272px;
	margin: auto;
	margin-top: 18px;
}

.o-event__point {
	padding: 48px 24px;
	background: #e3f4f8;
}

.o-event__point__balloon {
	border-radius: 8px;
	background: var(--lp-style-key-white, #fff);
	display: flex;
	padding: 14px 8px 18px 8px;
	flex-direction: column;
	align-items: center;
	gap: -3px;
	align-self: stretch;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		bottom: -20px;
		margin: auto;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 20px 16px 0 16px;
		border-color: #fff transparent transparent transparent;
	}
	p {
		color: var(--lp-style-key-magenta, #e065be);
		text-align: center;
		font-family: g.$fontFamily__Heading;
		font-size: 24px;
		font-style: normal;
		font-weight: 900;
		line-height: 1.4; /* 33.6px */
		letter-spacing: -0.24px;
	}
	.--small {
		font-size: 17px;
		font-weight: 700;
	}
}

.m-event__point__img {
  width: 100%;
	max-width: 328px;
	margin: auto;
	margin-top: 30px;
	border-radius: 16px;
	border: 4px solid var(--lp-style-key-white, #fff);
	background: rgba(147, 229, 229, 0.85);
	overflow: hidden;
}
.o-event__point__sub {
	color: var(--lp-style-key-black, #111);
	text-align: center;
	font-size: 18px;
	font-style: normal;
	font-weight: 600;
	line-height: 1.6; /* 28.8px */
	margin-top: 26px;
	.--note {
		vertical-align: super;
		font-size: 0.5em;
	}
}
.o-event__point__title {
	color: var(--lp-style-key-magenta, #e065be);
	text-align: center;
	font-family: g.$fontFamily__Heading;
	font-size: 26px;
	font-style: normal;
	font-weight: 900;
	line-height: 1.6; /* 41.6px */
	.--small {
		font-size: 22px;
	}
	.--note {
		vertical-align: super;
		font-size: 0.5em;
	}
	.--marker {
		display: inline-block;
		position: relative;
    z-index: 1;
		// background:linear-gradient(transparent calc(32.6/41.6*100%), var(--lp-style-key-yellow, #F2FF59) calc(32.6/41.6*100%));
		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 9px;
			position: absolute;
			left: 0;
			right: 0;
			margin: auto;
			bottom: 2px;
			background: var(--lp-style-key-yellow, #f2ff59);
      z-index: -1;
		}
	}
}
.o-event__point__text {
	color: var(--lp-style-key-black, #111);
	text-align: center;
	font-size: 20px;
	font-style: normal;
	font-weight: 600;
	line-height: 1.6; /* 32px */
	margin-top: 12px;
}
.o-event__point__note {
	text-align: left;
	color: var(--lp-style-key-black, #111);
	font-feature-settings: 'clig' off, 'liga' off;
	font-size: 12px;
	font-weight: 300;
	line-height: 1.6; /* 19.2px */
	padding-left: 26px;
	position: relative;
	margin-top: 16px;
  & + .o-event__point__note{
    margin-top: 4px;
  }
	span {
		position: absolute;
		top: 0;
		left: 0;
	}
}
