$color_1: inherit;
$color_2: currentcolor;
$font_family_1: monospace, monospace;
$background_color_1: transparent;

html {
	-webkit-text-size-adjust: 100%;
	box-sizing: border-box;
	-moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;
	word-break: normal;
	[type="button"] {
		-webkit-appearance: button;
	}
}
* {
	background-repeat: no-repeat;
	box-sizing: inherit;
	margin: 0;
	padding: 0;
}
::after {
	background-repeat: no-repeat;
	box-sizing: inherit;
	text-decoration: inherit;
	vertical-align: inherit;
}
::before {
	background-repeat: no-repeat;
	box-sizing: inherit;
	text-decoration: inherit;
	vertical-align: inherit;
}
hr {
	height: 0;
	overflow: visible;
}
details {
	display: block;
}
main {
	display: block;
}
summary {
	display: list-item;
}
small {
	font-size: 80%;
}
[hidden] {
	display: none;
}
abbr[title] {
	border-bottom: none;
	text-decoration: underline;
	-webkit-text-decoration: underline dotted;
	text-decoration: underline dotted;
}
a {
	background-color: $background_color_1;
	&:active {
		outline-width: 0;
	}
	&:hover {
		outline-width: 0;
	}
}
code {
	font-family: $font_family_1;
}
kbd {
	font-family: $font_family_1;
}
pre {
	font-family: $font_family_1;
	font-size: 1em;
}
samp {
	font-family: $font_family_1;
}
b {
	font-weight: bolder;
}
strong {
	font-weight: bolder;
}
sub {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
	bottom: -.25em;
}
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
	top: -.5em;
}
input {
	border-radius: 0;
	font: inherit;
	background-color: $background_color_1;
	border-style: none;
}
[disabled] {
	cursor: default;
}
[type="number"] {
	&::-webkit-inner-spin-button {
		height: auto;
	}
	&::-webkit-outer-spin-button {
		height: auto;
	}
}
[type="search"] {
	-webkit-appearance: textfield;
	outline-offset: -2px;
	&::-webkit-search-decoration {
		-webkit-appearance: none;
	}
}
textarea {
	overflow: auto;
	resize: vertical;
	font: inherit;
	background-color: $background_color_1;
	border-style: none;
}
button {
	font: inherit;
	overflow: visible;
	text-transform: none;
	color: $color_1;
	cursor: pointer;
	-webkit-appearance: button;
	background-color: $background_color_1;
	border-style: none;
	&::-moz-focus-inner {
		border-style: none;
		padding: 0;
	}
	&:-moz-focusring {
		outline: 1px dotted ButtonText;
	}
}
optgroup {
	font: inherit;
	font-weight: 700;
}
select {
	font: inherit;
	text-transform: none;
	background-color: $background_color_1;
	border-style: none;
	-moz-appearance: none;
	-webkit-appearance: none;
	&::-ms-expand {
		display: none;
	}
	&::-ms-value {
		color: $color_2;
	}
}
[role="button"] {
	color: $color_1;
	cursor: pointer;
}
[type="button"] {
	color: $color_1;
	cursor: pointer;
	&::-moz-focus-inner {
		border-style: none;
		padding: 0;
		outline: 1px dotted ButtonText;
	}
}
[type="reset"] {
	color: $color_1;
	cursor: pointer;
	-webkit-appearance: button;
	&::-moz-focus-inner {
		border-style: none;
		padding: 0;
		outline: 1px dotted ButtonText;
	}
}
[type="submit"] {
	color: $color_1;
	cursor: pointer;
	-webkit-appearance: button;
	&::-moz-focus-inner {
		border-style: none;
		padding: 0;
		outline: 1px dotted ButtonText;
	}
}
legend {
	border: 0;
	color: $color_1;
	display: table;
	max-width: 100%;
	white-space: normal;
}
::-webkit-file-upload-button {
	-webkit-appearance: button;
	color: $color_1;
	font: inherit;
}
img {
	border-style: none;
}
progress {
	vertical-align: baseline;
}
[aria-busy="true"] {
	cursor: progress;
}
[aria-controls] {
	cursor: pointer;
}
[aria-disabled] {
	cursor: default;
}

summary{
	list-style-type: none;	//▶が実機で出現
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	cursor: pointer;
}
/* 一部ブラウザで消えなかった場合は以下も追記 */
summary::-webkit-details-marker {
  display: none;
}

*:focus {
  outline: none;
}

li {
  list-style-type: none;
}
@media screen {
	[hidden~="screen"] {
		display: inherit;
		&:not(:active) {
			&:not(:focus) {
				&:not(:target) {
					clip: rect(0 0 0 0) !important;
					position: absolute !important;
				}
			}
		}
	}
}
