@use './config' as g;
.sec-benefits {
	position: relative;
	overflow: hidden;
	margin-top: 60px;
  .m-sec__inner {
		padding-bottom: 48px;
	}
}

.o-benefits__content {
	margin-top: 22px;
	display: flex;
	flex-wrap: wrap;
	gap: 32px;
}
.m-benefits__contents {
  flex: auto;
	padding: 26px 0px;
	align-self: stretch;
	position: relative;
	border-radius: 18px;
	border: 4px solid var(--lp-style-key-aque, #93e5e5);
	background: #f2ff59;
	overflow: hidden;
	& > * {
		position: relative;
	}
	&::before {
		content: '';
		position: absolute;
		left: -91px;
		top: -153px;
		display: block;
		background-image: url(../img/bg_box_benefits.png);
		background-repeat: no-repeat;
		background-size: contain;
		width: 560px;
		height: 421px;
		transform: rotate(10deg);
		opacity: 0.7;
	}
	h3 {
		display: flex;
		height: 62px;
		padding: 4px 8px 6px 8px;
		align-items: center;
		gap: 6px;
		align-self: stretch;
		color: var(--lp-style-key-black, #111);
		font-family: g.$fontFamily__Heading;
		font-size: 19px;
		font-style: normal;
		font-weight: 900;
		line-height: 1.3; /* 26px */
    background: var(--lp-style-key-white, #FFF);
    text-align: left;
		.--num {
			color: var(--lp-style-key-magenta, #e065be);
			text-align: center;
			font-family: g.$fontFamily__Outfit;
			font-size: 58px;
			font-style: normal;
			font-weight: 700;
			line-height: 1; /* 58px */
      display: block;
      width: 40px;
		}
	}
	p {
    color: #111;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 1.7; /* 27.2px */
    margin-top: 8px;
	}
  .--note{
    font-size: 14px;
    font-weight: 300;
    line-height: 1.4; /* 19.6px */
  }
}

.m-benefits__contents__img{
  width: 100%;
  max-width: 294px;
  margin: auto;
  margin-top: 18px;
}
