@use './config' as g;

body {
	@media (max-width: g.$maxW__pc) {
	}
	@media (min-width: g.$minW__pc) {
		// min-width: 1000px;
	}
}
#vantan-main {
  position: relative;
	font-family: g.$fontFamily__base;
	color: g.$baseColor__black;
	font-weight: 300;
	font-size: 14px;
	line-height: 1.6;
	text-align: center;
	overflow: hidden;
	// text-align: left;

	// background-image: url('../img/bg_contens.jpg');
	// background-attachment: fixed;
	// // background-size: cover; //
	// background-position: top;
	// background-repeat: no-repeat;
}
.o-main__bg{
  position: fixed;
  // width: 100vw;
  // height: 100vh;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  margin: 0 calc(50% - 50vw);

  img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    object-position: top center;
    @media (min-width: 2001px) {
    }
  }
}

main {
	picture {
		display: block;
	}
	img {
		display: block;
		width: 100%;
		height: auto;
		object-fit: contain;
	}
	a {
		color: inherit;
		text-decoration: none;
    &:hover{
      opacity: .7;
    }
	}
}
.o-contents_wrap {
	background: #fff;
	margin: auto;
	max-width: 100%;
  position: relative;
  z-index: 1;
	@media (min-width: g.$minW__pc) {
		margin-top: 60px;
    margin-bottom: 100px;
		padding: 40px 32px 0;
		max-width: 440px;
	}
}
.u-font-heading {
	font-family: g.$fontFamily__Heading;
}
.u-font-heading-en {
	font-family: g.$fontFamily__Alphabet;
}
.paragraph {
	display: block;
}
.br-pc {
	display: none;
	@media (min-width: 769px) {
		display: block;
	}
}
.br-sp {
	display: block;
	@media (min-width: 769px) {
		display: none;
	}
}
.m-sec__inner {
	width: 100%;
	padding: 0 24px;
}
.m-heading__wrap {
	position: relative;
}
.m-heading__img{
  width: 500px;
  margin: 0 calc(50% - 250px);
}
.m-heading__text {

	position: absolute;
	width: 100%;
	max-width: 500px;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	margin: auto;
	display: flex;
	align-items: center;
	padding: 0 20px;
  h2{
    color: var(--lp-style-key-magenta, #e065be);
    font-family: g.$fontFamily__Outfit;
    font-size: 47px;
    font-style: normal;
    font-weight: 700;
    line-height: 1; /* 47px */
    text-shadow: 2px 2px 0px #ffffff, -2px 2px 0px #ffffff, 2px -2px 0px #ffffff,
		-2px -2px 0px #ffffff, 2px 0px 0px #ffffff, 0px 2px 0px #ffffff,
		-2px 0px 0px #ffffff, 0px -2px 0px #ffffff;
  }
  p{
    font-family: g.$fontFamily__Heading;
    color: var(--lp-style-key-black, #111);
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 1; /* 20px */
    letter-spacing: -0.2px;
    margin-top: 6px;
  }
	.--left__text & {
		text-align: left;
		justify-content: flex-start;
	}
	.--right__text & {
		text-align: right;
		justify-content: flex-end;
	}
}
