@use './config' as g;
.sec-main {
	position: relative;
  overflow: hidden;
	.o-main__textwrap {
		position: absolute;
		top: 150px;
		left: 0;
		right: 0;
		padding-left: 12px;
		margin: auto;
		max-width: 375px;
	}
}
.o-mv{
  margin: 0 calc(50% - 250px);
  width: 500px;
}
.m-main__intro {
  position: relative;
	text-align: center;
	// text-shadow:  0px 0 5px #fff;
	font-size: 23px;
	font-style: normal;
	font-weight: 700;
	line-height: 1.4; /* 32.2px */
	letter-spacing: 0.23px;
	text-shadow: 0px 0px 5px #fff;
  filter:drop-shadow(0px 0px 5px #fff);
  color: #fff;

	span {
    color: #c1469f;
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		margin: auto;
		text-shadow: 2px 2px 0px #ffffff, -2px 2px 0px #ffffff, 2px -2px 0px #ffffff,
			-2px -2px 0px #ffffff, 2px 0px 0px #ffffff, 0px 2px 0px #ffffff,
			-2px 0px 0px #ffffff, 0px -2px 0px #ffffff;
	}
}
.m-main__sub {
	position: relative;
	text-align: center;
	// text-shadow:  0px 0 5px #fff;
	font-size: 30px;
	font-style: normal;
	font-weight: 700;
	line-height: 1.4; /* 32.2px */
	letter-spacing: 0.23px;
	text-shadow: 0px 0px 5px #fff;
  filter:drop-shadow(0px 0px 5px #fff);
  color: #fff;

	span {
    color: #c1469f;
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		margin: auto;
		text-shadow: 2px 2px 0px #ffffff, -2px 2px 0px #ffffff, 2px -2px 0px #ffffff,
			-2px -2px 0px #ffffff, 2px 0px 0px #ffffff, 0px 2px 0px #ffffff,
			-2px 0px 0px #ffffff, 0px -2px 0px #ffffff;
	}
}
.m-main__title{
  max-width: 262px;
  margin: auto;
  margin-top: 4px;
}
.m-main__badge{
  max-width: 126px;
  margin-top: 26px;
}
