@use './config' as g;
.sec-line {
	position: relative;
	overflow: hidden;
  .m-sec__inner {
		padding-bottom: 64px;
	}
}
.o-line__text {
	color: var(--lp-style-key-black, #111);
	text-align: center;
	font-size: 16px;
	font-style: normal;
	font-weight: 600;
	line-height: 1.8; /* 28.8px */
	margin-top: 32px;
}
.o-line__linkwrap {
	display: flex;
	flex-wrap: wrap;
	gap: 16px;
	margin-top: 32px;
}
.m-line__link {
	flex: auto;
	a {
		display: flex;
		height: 102px;
		padding: 0px 16px 2px 16px;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		gap: 4px;
		align-self: stretch;
		border-radius: 10px;
		border: 3px solid var(--lp-style-key-yellow, #f2ff59);
		background: var(--lp-style-key-line-color, #06c755);
		position: relative;
		&::after {
			content: '';
			display: block;
			width: 18px;
			height: 18px;
			position: absolute;
			right: 8px;
			bottom: 9px;
			background-image: url(../img/icon_arrow_yellow.svg);
			background-repeat: no-repeat;
			background-size: contain;
		}
    span{
      color: var(--lp-style-key-white, #FFF);
      text-align: center;
      font-family: g.$fontFamily__Heading;
      font-size: 18px;
      font-style: normal;
      font-weight: 700;
      line-height: 1.2; /* 21.6px */
    }
    .--em{
      font-size: 22px;
      font-weight: 900;
    }
	}
}
